@import url(https://fonts.googleapis.com/css?family=PT+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: #f9f9f9;
    font-family: 'Lato', sans-serif;
}
.login-form {
    text-align: center !important;
}

.pt-mono {
    font-family: 'PT Mono', monospace;
}
.login-container {
    height: 100vh;
    text-align: center;
}
.login-logo-container {
    text-align: center
}
.login-form-container {
    border: 1px solid black;
    width: 20rem;
    margin: 0 auto;
    border-radius: 15px;
    border-color: #e9e9e9;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.18);
    color: #26385F;
}

.login-form input {
    background: white;
    border-radius: 15px;
    padding: 0.5rem;
    border: 2px solid #d6d6d6;
    text-align: center;
    color: #26385F;
}
.login-form input::-webkit-input-placeholder {
    color: #26385F;
    font-size: 1.25rem;
}
.login-form input::-moz-placeholder {
    color: #26385F;
    font-size: 1.25rem;
}
.login-form input:-ms-input-placeholder {
    color: #26385F;
    font-size: 1.25rem;
}
.login-form input::-ms-input-placeholder {
    color: #26385F;
    font-size: 1.25rem;
}
.login-form input::placeholder {
    color: #26385F;
    font-size: 1.25rem;
}

.pill {
    border-radius: 50px;
}
nav {
    box-shadow: 0px 11.7288px 15.9504px rgba(0, 0, 0, 0.05);
  }
  .nav-link {
    color: #EF6E46 !important;
    padding-right: 1rem;
    padding-left: 1rem
  }
  .navbar-brand img {
    width: 12rem;
  }
  .navbar-toggler {
    border: none;
  }
  
  .navbar-collapse {
    text-align: right !important;
  }
  .pay-rate-input {
    border: none;
    width: 3rem;
    color:#EF6E46;
    background-color: inherit;
    padding: 0
  }
  
  .pay-rate-input:focus {
    outline: none;
  }

  .pay-rate-input input::-webkit-outer-spin-button, .pay-rate-input input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  .nav-link {
    cursor: pointer;
  }
.entries-heading-container {
    padding-top: 2rem;
    padding-left: 2rem;
    margin-bottom: 5%
}
.entries-heading {
    font-size: 24px;
    font-weight: bold;
}
.pay-period-btn {
    font-size: 10px;
    padding: 5px;
    padding-left: 7px;
    padding-right: 7px;
    background-color: #26385F;
    border: none;
    margin-left: 1rem;
    top: -15%;
    position: relative;
}
.inline {
    display: inline;
}
select {
    background: #26385F;
    color: white;
    border-radius: 15px;
    padding: 0.25rem
}
input {
    background: #26385F;
    color: white;
    border-radius: 15px;
    padding: 0.25rem;
    border: none
}
input::-webkit-input-placeholder {
    color: white
}
input::-moz-placeholder {
    color: white
}
input:-ms-input-placeholder {
    color: white
}
input::-ms-input-placeholder {
    color: white
}
input::placeholder {
    color: white
}
.entry {
    margin-left: 2rem;
    margin-right: 10%;
    margin-bottom: 10%;
    box-shadow: 0px 5.08622px 20.3449px rgba(0, 0, 0, 0.1);
    border-radius: 15px 15px 15px 15px;
}
.entry-left {

}
.approval {
    position: absolute;
    top: 30%;
}
.entry-right {
    border-radius: 0 15px 15px 0;
    color: white
}
.day {
    font-size: 1.5rem;
    display: inline;
}
.date {

}
.hours {
    font-size: 2rem;
}


.monday-right {
    background-color: #73A065;
}
.monday-left, .monday {
    color: #73A065
}
.tuesday-right {
    background-color: #335FBE;
}
.tuesday-left, .tuesday {
    color: #335FBE
}
.wednesday-right {
    background-color: #EF6E46;
}
.wednesday-left, .wednesday {
    color: #EF6E46
}
.thursday-right {
    background-color: #FFBA5F;
}
.thursday-left, .thursday {
    color: #FFBA5F
}
.friday-right {
    background-color: #BF84C9;
}
.friday-left, .friday {
    color: #BF84C9
}
.saturday-right {
    background-color: #c9c9c9;
}
.saturday-left {
    color: #c9c9c9
}
.sunday-right {
    background-color: #c9c9c9;
}
.sunday-left {
    color: #c9c9c9
}

#time-card input, #time-card select, .punch input, .punch select {
    width: 85%;
    text-align: center;
    background-color: #335FBE;
    color: white;
    font-size: .8rem
}
.w-85 {
    width: 85%
}
.add-punch {
    width: 100%;
    text-transform: uppercase;
}
.black {
    color: black
}

.edit-entry-footer {
    display: block;
    text-align: center;
    border: none
}
.edit-entry-header {
    border: none
}
.border-radius-15 {
    border-radius: 15px
}
.save {
    background-color: #283770;
    color: white
}
.approve-button, .time-card-save {
    background-color: #73A065;
    color: white;
}
.punch-title {
    font-size: .92rem;
}
.delete {
    cursor: pointer;
}
.greeting-container {
    padding-left: 2rem;
}
.greeting {
    font-weight: bold;
}
.greeting-content {
    font-size: 18px;
}
.take-home {
    padding-top: 1rem;
}
.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-body {
  padding-top: 7rem;
  color: #26385F
}

.App-link {
  color: #09d3ac;
}

.btn-orange {
  background-color: #EF6E46;
  color: white;
}
.btn-orange:hover {
  background-color: #26385F;
  color: white 
}

.orange-text {
  color: #EF6E46
}
.yellow-text {
  color: #FFBA5F
}

.greeting {
  font-weight: bold;
}
.greeting-content {
  font-size: 18px;
}
.take-home {
  padding-top: 1rem;
}

